import React from "react";
import svgBack from "../Images/lowPolyGrid.svg";
import { IssueHound } from "../Components/Animations";

function HomePage() {
  return (
    <>
      <div>
        <div className="polyGrid">
          <img
            style={{
              opacity: "0.8",
              width: "100vw",
              left: "0.36vw",
              position: "sticky",
              overflowY: "scroll",
            }}
            src={svgBack}
            alt="SVG"
          />
        </div>
        <IssueHound />
        <button className="buttonSignUp">Sign Up In Development</button>
        <img
        className="houndImage"
        src={require("../Images/hound.png")}
      />
      </div>
    </>
  );
}

export default HomePage;
