import React, { useState } from 'react';
import GridLayout from 'react-grid-layout';

function CalendarPage() {
  const [layout, setLayout] = useState([
      { i: 'day1', x: 0, y: 0, w: 1, h: 1 },
      { i: 'day2', x: 1, y: 0, w: 1, h: 1 },
      { i: 'day3', x: 2, y: 0, w: 1, h: 1 },
      { i: 'day4', x: 3, y: 0, w: 1, h: 1 },
      { i: 'day5', x: 4, y: 0, w: 1, h: 1 },
      { i: 'day6', x: 5, y: 0, w: 1, h: 1 },
      { i: 'day7', x: 6, y: 0, w: 1, h: 1 },
      { i: 'day8', x: 0, y: 0, w: 1, h: 1 },
      { i: 'day9', x: 1, y: 0, w: 1, h: 1 },
      { i: 'day10', x: 2, y: 0, w: 1, h: 1 },
      { i: 'day11', x: 3, y: 0, w: 1, h: 1 },
      { i: 'day12', x: 4, y: 0, w: 1, h: 1 },
      { i: 'day13', x: 5, y: 0, w: 1, h: 1 },
      { i: 'day14', x: 6, y: 0, w: 1, h: 1 },
      { i: 'day15', x: 0, y: 0, w: 1, h: 1 },
      { i: 'day16', x: 1, y: 0, w: 1, h: 1 },
      { i: 'day17', x: 2, y: 0, w: 1, h: 1 },
      { i: 'day18', x: 3, y: 0, w: 1, h: 1 },
      { i: 'day19', x: 4, y: 0, w: 1, h: 1 },
      { i: 'day20', x: 5, y: 0, w: 1, h: 1 },
      { i: 'day21', x: 6, y: 0, w: 1, h: 1 },
      { i: 'day22', x: 0, y: 0, w: 1, h: 1 },
      { i: 'day23', x: 1, y: 0, w: 1, h: 1 },
      { i: 'day24', x: 2, y: 0, w: 1, h: 1 },
      { i: 'day25', x: 3, y: 0, w: 1, h: 1 },
      { i: 'day26', x: 4, y: 0, w: 1, h: 1 },
      { i: 'day27', x: 5, y: 0, w: 1, h: 1 },
      { i: 'day28', x: 6, y: 0, w: 1, h: 1 },
      { i: 'day29', x: 0, y: 0, w: 1, h: 1 },
      { i: 'day30', x: 1, y: 0, w: 1, h: 1 },
      { i: 'day31', x: 2, y: 0, w: 1, h: 1 }
    ]);

  const renderCalendar = () => {
    return layout.map((day, index) => {
      return (
        <div key={day.i} style={{ backgroundColor: 'white' }}>
          {index + 1}
        </div>
      );
    });
  };

  return (
    <GridLayout className="layout" layout={layout} cols={7} rowHeight={90} width={1100}>
      {renderCalendar()}
    </GridLayout>
  );
}

export default CalendarPage;
